"use client";

import { useState, useEffect } from "react";
import { checkCodeChefUser, checkCodeforcesUser, checkLeetCodeUser } from "@/actions/platform.action";
import { Loader2, ExternalLink } from "lucide-react";
import {
    LineChart,
    Line,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    ResponsiveContainer
} from "recharts";

interface ProblemOverviewCardProps {
    solvedByDifficulty: {
        EASY: number;
        MEDIUM: number;
        HARD: number;
    };
    totalProblems: {
        EASY: number;
        MEDIUM: number;
        HARD: number;
        TOTAL: number;
    };
    problemsSolved: number;
    leetCodeHandle?: string | null;
    codeChefHandle?: string | null;
    codeforcesHandle?: string | null;
}

export function ProblemOverviewCard({
    solvedByDifficulty,
    totalProblems,
    problemsSolved,
    leetCodeHandle,
    codeChefHandle,
    codeforcesHandle
}: ProblemOverviewCardProps) {
    const [activeTab, setActiveTab] = useState<"overview" | "leetcode" | "codechef" | "codeforces">("overview");

    // Existing Logic for Internal Overview
    const calculatedSolved = solvedByDifficulty.EASY + solvedByDifficulty.MEDIUM + solvedByDifficulty.HARD;
    const total = Math.max(totalProblems.TOTAL || 1, 1);
    const percentage = Math.round((calculatedSolved / total) * 100);
    const easyPct = (solvedByDifficulty.EASY / total) * 100;
    const medPct = (solvedByDifficulty.MEDIUM / total) * 100;
    const hardPct = (solvedByDifficulty.HARD / total) * 100;
    const radius = 100;
    const circumference = 2 * Math.PI * radius;
    const easyArc = (easyPct / 100) * circumference;
    const medArc = (medPct / 100) * circumference;
    const hardArc = (hardPct / 100) * circumference;

    return (
        <div className="bg-white dark:bg-[#141414] rounded-2xl border border-dashed border-gray-300 dark:border-[#262626] p-6 hover:shadow-md transition-all h-[400px] flex flex-col">
            <div className="flex items-center justify-between mb-6">
                <h3 className="text-lg font-bold text-gray-900 dark:text-gray-100">Problem Solving Overview</h3>
            </div>

            {/* Tabs */}
            <div className="flex space-x-2 mb-6 overflow-x-auto pb-2 scrollbar-hide">
                 {/* Tabs implementation code... */}
                 <TabButton active={activeTab === "overview"} onClick={() => setActiveTab("overview")}>Overview</TabButton>
                 <TabButton active={activeTab === "leetcode"} onClick={() => setActiveTab("leetcode")}>LeetCode</TabButton>
                 <TabButton active={activeTab === "codechef"} onClick={() => setActiveTab("codechef")}>CodeChef</TabButton>
                 <TabButton active={activeTab === "codeforces"} onClick={() => setActiveTab("codeforces")}>CodeForces</TabButton>
            </div>

            <div className="flex-1 min-h-0">
                {activeTab === "overview" && (
                    <div className="flex flex-col-reverse sm:flex-row items-center justify-between gap-8 h-full">
                         <div className="w-full sm:w-auto min-w-[140px] space-y-3 self-center">
                            <LegendItem color="bg-green-500" label="Easy" value={solvedByDifficulty.EASY} total={totalProblems.EASY} />
                            <LegendItem color="bg-orange-500" label="Med." value={solvedByDifficulty.MEDIUM} total={totalProblems.MEDIUM} />
                            <LegendItem color="bg-red-500" label="Hard" value={solvedByDifficulty.HARD} total={totalProblems.HARD} />
                        </div>

                        <div className="relative flex-1 flex items-center justify-center h-full">
                            <div className="relative w-48 h-48 md:w-56 md:h-56">
                                <svg className="w-full h-full transform -rotate-90">
                                    <circle cx="50%" cy="50%" r={radius} fill="none" className="stroke-gray-100 dark:stroke-[#262626]" strokeWidth="20" />
                                    <circle cx="50%" cy="50%" r={radius} fill="none" stroke="#22c55e" strokeWidth="20" strokeDasharray={circumference} strokeDashoffset={circumference - easyArc} strokeLinecap="round" />
                                    <circle cx="50%" cy="50%" r={radius} fill="none" stroke="#f97316" strokeWidth="20" strokeDasharray={circumference} strokeDashoffset={circumference - medArc} strokeLinecap="round" style={{ transform: `rotate(${(easyPct / 100) * 360}deg)`, transformOrigin: "50% 50%" }} />
                                    <circle cx="50%" cy="50%" r={radius} fill="none" stroke="#ef4444" strokeWidth="20" strokeDasharray={circumference} strokeDashoffset={circumference - hardArc} strokeLinecap="round" style={{ transform: `rotate(${((easyPct + medPct) / 100) * 360}deg)`, transformOrigin: "50% 50%" }} />
                                </svg>
                                <div className="absolute inset-0 flex flex-col items-center justify-center pointer-events-none">
                                    <span className="text-4xl font-extrabold text-gray-900 dark:text-gray-100 tracking-tight">{percentage}%</span>
                                    <span className="text-xs font-medium text-gray-400 uppercase tracking-wider mt-1">Completed</span>
                                    <span className="text-sm font-medium text-gray-500 mt-2">{calculatedSolved} / {total}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                )}

                {activeTab === "leetcode" && <LeetCodeView handle={leetCodeHandle} />}
                {activeTab === "codechef" && <CodeChefView handle={codeChefHandle} />}
                {activeTab === "codeforces" && <CodeForcesView handle={codeforcesHandle} />}
            </div>
        </div>
    );
}

function TabButton({ active, onClick, children }: { active: boolean; onClick: () => void; children: React.ReactNode }) {
    return (
        <button
            onClick={onClick}
            className={`px-3 py-1.5 rounded-full text-xs font-medium transition-colors whitespace-nowrap ${
                active
                    ? "bg-black dark:bg-white text-white dark:text-black"
                    : "bg-gray-100 dark:bg-[#262626] text-gray-600 dark:text-gray-400 hover:bg-gray-200 dark:hover:bg-[#333]"
            }`}
        >
            {children}
        </button>
    );
}

function LegendItem({ color, label, value, total }: { color: string; label: string; value: number; total?: number }) {
    return (
        <div className="flex items-center justify-between p-2 rounded-xl bg-white dark:bg-[#1a1a1a] border border-gray-100 dark:border-[#262626]">
            <div className="flex items-center gap-2">
                <div className={`w-1.5 h-1.5 rounded-full ${color}`} />
                <span className={`text-xs font-semibold ${color.replace("bg-", "text-").replace("500", "600")} dark:${color.replace("bg-", "text-").replace("500", "500")}`}>{label}</span>
            </div>
            <div className="text-right flex items-baseline gap-1 pl-4">
                <span className="text-sm font-bold text-gray-900 dark:text-gray-100">{value}</span>
                {total !== undefined && <span className="text-[10px] text-gray-400">/ {total}</span>}
            </div>
        </div>
    );
}

// --- Sub-Views ---

function LeetCodeView({ handle }: { handle?: string | null }) {
    const [data, setData] = useState<any>(null);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        if (handle) {
            setLoading(true);
            checkLeetCodeUser(handle).then(res => {
                if (res.success) setData(res);
                setLoading(false);
            });
        }
    }, [handle]);

    if (!handle) return <div className="h-full flex items-center justify-center text-gray-500 text-sm">No LeetCode handle linked</div>;
    if (loading) return <div className="h-full flex items-center justify-center"><Loader2 className="w-6 h-6 animate-spin text-gray-400" /></div>;
    if (!data) return <div className="h-full flex items-center justify-center text-gray-500 text-sm">Failed to load data</div>;

    const easy = data.submitStats?.acSubmissionNum?.find((s: any) => s.difficulty === "Easy")?.count || 0;
    const medium = data.submitStats?.acSubmissionNum?.find((s: any) => s.difficulty === "Medium")?.count || 0;
    const hard = data.submitStats?.acSubmissionNum?.find((s: any) => s.difficulty === "Hard")?.count || 0;
    const total = data.submitStats?.acSubmissionNum?.find((s: any) => s.difficulty === "All")?.count || 0;

    // Total available questions (approximate, hardcoded or if API provided)
    // LeetCode API sometimes provides total questions count in `allQuestionsCount` but I didn't fetch it.
    // I'll just show solved counts.

    return (
        <div className="flex flex-col-reverse sm:flex-row items-center justify-between gap-8 h-full">
            <div className="w-full sm:w-auto min-w-[140px] space-y-3 self-center">
                 <LegendItem color="bg-green-500" label="Easy" value={easy} />
                 <LegendItem color="bg-orange-500" label="Med." value={medium} />
                 <LegendItem color="bg-red-500" label="Hard" value={hard} />
            </div>
             <div className="relative flex-1 flex items-center justify-center h-full">
                <div className="flex flex-col items-center">
                     <span className="text-4xl font-bold text-gray-900 dark:text-white">{total}</span>
                     <span className="text-xs text-gray-500 uppercase tracking-wider">Solved</span>
                </div>
            </div>
        </div>
    );
}

function CodeChefView({ handle }: { handle?: string | null }) {
     const [data, setData] = useState<any>(null);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        if (handle) {
            setLoading(true);
            checkCodeChefUser(handle).then(res => {
                if (res.success) setData(res);
                setLoading(false);
            });
        }
    }, [handle]);

    if (!handle) return <div className="h-full flex items-center justify-center text-gray-500 text-sm">No CodeChef handle linked</div>;
    if (loading) return <div className="h-full flex items-center justify-center"><Loader2 className="w-6 h-6 animate-spin text-gray-400" /></div>;
    if (!data) return <div className="h-full flex items-center justify-center text-gray-500 text-sm">Failed to load data</div>;

    return (
        <div className="h-full w-full flex flex-col gap-4">
            <div className="flex justify-between items-start">
                 <div className="grid grid-cols-2 gap-4 w-full">
                    <div className="bg-gray-50 dark:bg-[#1a1a1a] p-3 rounded-xl border border-gray-100 dark:border-[#262626]">
                        <div className="text-xs text-gray-500">Current Rating</div>
                        <div className="text-lg font-bold text-gray-900 dark:text-white">{data.currentRating}</div>
                         <div className="text-[10px] text-gray-400">{data.stars}</div>
                    </div>
                     <div className="bg-gray-50 dark:bg-[#1a1a1a] p-3 rounded-xl border border-gray-100 dark:border-[#262626]">
                        <div className="text-xs text-gray-500">Global Rank</div>
                        <div className="text-lg font-bold text-gray-900 dark:text-white">{data.globalRank}</div>
                    </div>
                      <div className="bg-gray-50 dark:bg-[#1a1a1a] p-3 rounded-xl border border-gray-100 dark:border-[#262626]">
                        <div className="text-xs text-gray-500">Problems Solved</div>
                        <div className="text-lg font-bold text-gray-900 dark:text-white">{data.fullySolvedCount}</div>
                    </div>
                      <div className="bg-gray-50 dark:bg-[#1a1a1a] p-3 rounded-xl border border-gray-100 dark:border-[#262626]">
                        <div className="text-xs text-gray-500">Contests</div>
                        <div className="text-lg font-bold text-gray-900 dark:text-white">{data.ratingData?.length || 0}</div>
                    </div>
                 </div>
            </div>

            {/* Simple Rating Graph */}
            {data.ratingData && data.ratingData.length > 0 && (
                <div className="flex-1 w-full min-h-0">
                    <ResponsiveContainer width="100%" height="100%">
                        <LineChart data={data.ratingData}>
                             <Tooltip
                                contentStyle={{ backgroundColor: '#262626', borderColor: '#333', borderRadius: '8px', color: '#fff' }}
                                itemStyle={{ color: '#fff' }}
                                labelStyle={{ display: 'none' }}
                             />
                            <Line type="monotone" dataKey="rating" stroke="#f97316" strokeWidth={2} dot={false} />
                        </LineChart>
                    </ResponsiveContainer>
                </div>
            )}
        </div>
    );
}

function CodeForcesView({ handle }: { handle?: string | null }) {
    const [data, setData] = useState<any>(null);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        if (handle) {
            setLoading(true);
            checkCodeforcesUser(handle).then(res => {
                if (res.success) setData(res);
                setLoading(false);
            });
        }
    }, [handle]);

    if (!handle) return <div className="h-full flex items-center justify-center text-gray-500 text-sm">No CodeForces handle linked</div>;
    if (loading) return <div className="h-full flex items-center justify-center"><Loader2 className="w-6 h-6 animate-spin text-gray-400" /></div>;
    if (!data) return <div className="h-full flex items-center justify-center text-gray-500 text-sm">Failed to load data</div>;

    const solved = data.solvedByDifficulty || { EASY: 0, MEDIUM: 0, HARD: 0, TOTAL: 0 };


    return (
        <div className="h-full w-full flex flex-col gap-4">
             <div className="flex justify-between items-start">
                 <div className="grid grid-cols-2 gap-4 w-full">
                    <div className="bg-gray-50 dark:bg-[#1a1a1a] p-3 rounded-xl border border-gray-100 dark:border-[#262626]">
                        <div className="text-xs text-gray-500">Rating</div>
                        <div className="text-lg font-bold text-gray-900 dark:text-white">{data.rating || "Unrated"}</div>
                         <div className="text-[10px] text-gray-400">{data.rank || "-"}</div>
                    </div>
                    <div className="bg-gray-50 dark:bg-[#1a1a1a] p-3 rounded-xl border border-gray-100 dark:border-[#262626]">
                        <div className="text-xs text-gray-500">Max Rating</div>
                        <div className="text-lg font-bold text-gray-900 dark:text-white">{data.maxRating || "Unrated"}</div>
                    </div>
                 </div>
            </div>

            <div className="flex flex-col-reverse sm:flex-row items-center justify-between gap-8 h-full flex-1">
                 <div className="w-full sm:w-auto min-w-[140px] space-y-3 self-center">
                    <LegendItem color="bg-green-500" label="Easy (<1200)" value={solved.EASY} />
                    <LegendItem color="bg-orange-500" label="Med. (1200-1600)" value={solved.MEDIUM} />
                    <LegendItem color="bg-red-500" label="Hard (1600+)" value={solved.HARD} />
                </div>
                 <div className="relative flex-1 flex items-center justify-center h-full">
                    <div className="flex flex-col items-center">
                         <span className="text-4xl font-bold text-gray-900 dark:text-white">{solved.TOTAL}</span>
                         <span className="text-xs text-gray-500 uppercase tracking-wider">Total Solved</span>
                    </div>
                </div>
            </div>
        </div>
    );
}
